
// 子应用列表
const apps = []

/**
 * registerApplication 注册加载子应用
 * @export
 * @param {*} appName
 * @param {*} loadApp
 * @param {*} activeWhen
 * @param {*} customProps
 */
export function registerApplication (appName, loadApp, activeWhen, customProps) {
  apps.push({
    name: appName,
    loadApp, activeWhen, customProps
  })
  console.log('app',apps)
}
